.fg {
  position: relative;
  margin: 0;
  textarea,
  input {
    border: 1px solid $gray400;
    padding: 0 .75em;
    height: 2.375em;
    width: 100%;
    font-size: 1em;
    line-height: 150%;
    background-color: #fff;
    color: $black;
    border-radius: 0.25em;
    &::placeholder {
      color: $gray600;
    }
    &:focus {
      border-color: $primary;
    }
    &:disabled{
      background-color: $gray200;
    }
  }
  textarea{
    padding-bottom: .375em;
    padding-top: .375em;
    height: 5.375em;
    resize: vertical;
  }
  label{
    display: block;
    font-size: 1em;
    line-height: 150%;
    margin-bottom: .5em;
  }
  &._required{
    label:after{
      content: '*';
      color: $danger;
      margin-left: .25em;
    }
  }
  &__icon{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: .8em;
    width: 1em;
    @include center;
    img{
      width: 100%;
      display: block;
    }
    &~input{
      padding-left: 2em;
    }
  }
}

.date-input{
  background-image: url(../img/calendar.svg);
  background-repeat: no-repeat;
  background-position: right .81em center;
  background-size: 1em auto;
  padding-right: 2em;
}

.m-check,
.m-radio {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:hover+label {
      &::before {
        box-shadow: 0 0 0 .25em rgba(253,195,5,.25);
      }
    }
  }
  label {
    padding-left: 1.5em;
    position: relative;
    min-height: 1em;
    font-size: 1em;
    line-height: 150%;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border: 1px solid rgba(0, 0, 0, 0.15);
      width: 1em;
      height: 1em;
      left: 0;
      top: 0;
      transform: translateY(.125em);
      transition: all .100s;
    }
  }
}


.m-radio{
  label{
    &:before{
      border-radius: 50%;
    }
  }
  input{
    &:checked+label{
      &:before{
        border-width: .25em;
        border-color: $primary;
      }
    }
  }
}

.m-check{
  label{
    &:before{
      border-radius: .25em;
      @include bgImgSize(.6875em);
    }
  }
  input{
    &:checked+label{
      &:before{
        background-color: $primary;
        border-color: $primary;
        background-image: url(../img/check.svg);
      }
    }
  }
}

.select1{
  position: relative;

  img{
    width: 1em;
    position: absolute;
    left: .5em;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
  }
  .nice-select{
    padding: 0 .5em 0 2em;
    border: none;
    height: 1.9375em;
  }
}

.m-select{
  height: 1.9375em;
  width: unset;
  min-width: 100%;
  font-size: 1em;
  color: $gray700;
  display: flex;
  align-items: center;
  .current{
    font-size: .875em;
    line-height: 150%;
  }
  .list{
    margin-top: .1875em;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: .25em;
    padding: .5em 0;
    width: auto;
    min-width: 100%;
  }
  .option{
    border: none !important;
    display: flex;
    align-items: center;
    height: 2em;
    padding: 0 1em;
    font-size: 1em;
  }
}
