.embossing-main {
  padding: 0 0 0.5em;
  &__subtitle {
    margin-top: 1.25em;
  }
  &__text {
    margin-top: 0.3125em;
    p {
      font-size: 1em;
      line-height: 150%;
    }
  }
}
.embossing-products {
  padding: 0.5em 0;
}
.embossing-history {
  padding: 0.5em;
  &__text {
    margin-top: 0.3125em;
    p {
      font-size: 1em;
      line-height: 150%;
    }
  }
}
.embossing-video {
  padding: 0.5em 0 3em;
  &__inner {
    display: flex;
    flex-direction: column;
  }
  &__desc {
    margin-top: 1em;
  }
  &__text {
    margin-top: 0.3125em;
    p {
      font-size: 1em;
      line-height: 150%;
    }
  }
}
.video {
  display: block;
  width: 100%;
  height: 25em;
  background: #495057;
  border-radius: 0.25em;
  position: relative;
  img {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 6.5em;
  }
}

@media screen and (max-width: 992px) {
  .embossing-video {
    padding: 0.5em 0 1.5em;
  }
}

@media screen and (max-width: 576px) {
  .embossing-video {
    &__desc {
      order: -1;
      margin: 0 0 1em 0;
    }
  }
}