.orders{
  @include grid100gap(1.5em);
  max-width: 53.5em;
}

.orders-filter{
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1.5em;
  &__checks{
    display: flex;
    align-items: center;
    .m-check{
      @include lastM(0 1em 0 0);
    }
  }
  .select1{
    .list{
      left: unset;
      right: 0;
    }
  }
}

.order-card{
  padding: 1em;
  background-color: $gray100;
  border-radius: 1.25em;
  @include grid100gap(1em);
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__name{
    display: flex;
    align-items: center;
    img{
      width: 1em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__badges{
    display: flex;
    .badge{
      border-radius: .25em;
      @include lastM(0 1em 0 0);
    }
  }
  &__props{
    display: flex;
    align-items: center;
    justify-content: space-between;
    .props-item{
      margin: 0 !important;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    .m-btn{
      margin-right: .5em;
    }
  }
  &__hidden{
    display: none;
  }
}

.order-detail{
  padding-top: .5em;
  @include grid100gap(.5em);
  &__docs{
    display: flex;
    align-items: center;
    .doc-item{
      margin-right: .5em;
    }
  }
  &__images{
    margin-top: .5em;
    @include grid100gap(.5em);
  }
  &__agent{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
  }
}

.product-card4{
  border-radius: .25em;
  border: 1px solid $gray500;
  padding: 1em;
  display: flex;
  flex-direction: column;
  &__text{
    min-height: 2.375em;
  }
  .product-card2__prices{
    margin-top: auto;
    padding-top: .5em;
  }
}

.file-item{
  display: flex;
  align-items: center;
  padding: 0 .25em;
  height: 1.5em;
  color: $secondary;
  img{
    width: 1em;
    flex-shrink: 0;
    margin-right: .5em;
  }
  span{
    font-size: 1em;
    line-height: 150%;
  }
}



@media screen and (max-width: 992px){
  .orders-filter{
    display: none;
  }
}


@media screen and (max-width: 576px){
  .order-card{
    padding-top: 3.5em;
    position: relative;
    &__props{
      order: -1;
      @include grid100gap(1em);
    }
    &__name,
    &__phone{
      position: absolute;
      top: 1em;
    }
    &__phone{
      left: 1em;
    }
    &__name{
      right: 1em;
    }
    &__top{
      @include grid100gap(1em);
    }
    &__badges{
      order: -1;
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    &__nav{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .25em));
      gap: 1em .5em;
      width: 100%;
      margin-bottom: 1em;
      .m-btn{
        margin: 0;
      }
    }
    &__pay-btn{
      order: 2;
      grid-column: 1/-1;
    }
  }
}
