@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
.swiper{
  &-button{
    width: 2em;
    height: 2em;
    opacity: .5;
    img{
      width: 100%;
      display: block;
    }
    &.swiper-button-disabled{
      opacity: .5;
    }
    &:hover{
      opacity: 1;
    }
  }
}

.grid-column2,
.grid-column4,
.grid-column6{
  display: grid;
  gap: 1.5em;
}

.grid-column2{
  grid-template-columns: repeat(2,calc(50% - .75em));
}

.grid-column4{
  grid-template-columns: repeat(4,calc(25% - 4.5em / 4));
}

.grid-column6{
  grid-template-columns: repeat(6,calc(100% / 6 - 1.5em * 5 / 6));
}

._section{
  padding: 3em 0;
  &._section-first{
    padding-top: 0;
  }
  &__grid{
    margin-top: 1.5em;
  }
}

._slider{
  margin-top: 1.5em;
  overflow: hidden;
  position: relative;
  .swiper{
    &-container{
      width: calc(100% + 1.5em);
    }
    &-slide{
      padding-right: 1.5em;
    }
    &-button{
      margin: 0;
      transform: translateY(-50%);
      &-prev{
        left: 1em;
      }
      &-next{
        right: 1em;
      }
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.badge{
  @include center;
  border-radius: 6.25em;
  min-width: 2.725em;
  height: 1.275em;
  padding: 0 .49em;
  color: #fff;
  background-color: $secondary;
  &._warning{
    background-color: $warning;
    color: $black;
  }
  &._success{
    background-color: $success;
  }
}

.tabs{
  display: flex;
}

.tab1{
  height: 2.5em;
  padding: 0 1em;
  color: $gray700;
  border-radius: .25em;
  transition: all .3s;
  cursor: pointer;
  span{
    font-size: 1em;
    line-height: 150%;
  }
  &._active{
    pointer-events: none;
  }
  &:hover,
  &._active{
    background-color: $primary;
    color: $black;
  }
}

.spinner{
  width: 1em;
  height: 1em;
  flex-shrink: 0;
  border: .25em solid $warning;
  border-right-color: transparent;
  border-radius: 50%;
  animation: spin 1s infinite linear;
}

@keyframes spin {
  from{
    transform: rotate(0deg);
  }to{
     transform: rotate(360deg);
   }
}

.pagination{
  display: flex;
  flex-direction: column;
  align-items: center;
  &__wrap{
    margin-top: 1em;
    @include center;
  }
  ._view2{
    display: none;
  }
  &__nav{
    display: flex;
  }
  &__item{
    padding: 0 .5em;
    height: 1.9375em;
    @include center;
    border: 1px solid $gray300;
    color: $primary;
    position: relative;
    &:not(:last-child){
      margin-left: -1px;
    }
    &:nth-child(1){
      border-top-left-radius: .2em;
      border-bottom-left-radius: .2em;
    }
    &:last-child{
      border-top-right-radius: .2em;
      border-bottom-right-radius: .2em;
    }
    &:hover,
    &._active{
      z-index: 2;
      background-color: $primary;
      color: #fff;
      path{
        fill: #fff;
      }
    }
  }
  &__field{
    display: flex;
    align-items: center;
    margin-left: .5em;
    .fg{
      width: 3.125em;
      flex-shrink: 0;
      margin-left: .25em;
      height: 1.9375em;
      input{
        height: 100%;
        font-size: .875em;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .grid-column4{
    grid-template-columns: repeat(2,calc(50% - .75em));
  }

  .grid-column6{
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
  }

  ._section{
    padding: 1.5em 0;
    &._section-first{
      padding-top: 1em;
    }
  }

  ._slider{
    .swiper{
      &-button{
        display: none;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .grid-column2,
  .grid-column4{
    grid-template-columns: 100%;
  }

  .grid-column-mob2,
  .grid-column6{
    grid-template-columns: repeat(2,calc(50% - .75em));
  }

  .pagination{
    margin-top: 1.5em;
    ._view1{
      display: none;
    }
    ._view2{
      display: flex;
    }
    &__nav{
      margin-left: .5em;
    }
  }
}
