@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
    &:disabled{
        opacity: .65;
        pointer-events: none;
    }
}

.m-btn-medium{
    padding: 0 .75em;
    height: 2.375em;
    border-radius: 0.25em;
    span{
        font-size: 1em;
        line-height: 150%;
    }
}

.m-btn-small{
    padding: 0 .5em;
    height: 1.9375em;
    border-radius: 0.2em;
    span{
        font-size: .875em;
        line-height: 150%;
    }
    svg,
    img{
        width: .75em;
        height: auto;
        flex-shrink: 0;
        margin-right: .375em;
    }
}

.m-btn-primary{
    background-color: $primary;
    color: $black;
    &:hover{
        background-color: darken($primary,10%);
        color: $black;
    }
}


.m-btn-secondary{
    background-color: $secondary;
    color: #fff;
    &:hover{
        background-color: darken($secondary,10%);
        color: #fff;
    }
}

.m-btn-success{
    background-color: $success;
    color: #fff;
    &:hover{
        background-color: darken($success,10%);
        color: #fff;
    }
}

.m-btn-danger{
    background-color: $danger;
    color: #fff;
    &:hover{
        background-color: darken($danger,10%);
        color: #fff;
    }
}

.m-btn-secondary-outline{
    border: 1px solid $secondary;
    color: $secondary;
    &:hover{
        background-color: $secondary;
        color: #fff;
    }
}

.m-btn-danger-outline{
    border: 1px solid $danger;
    color: $danger;
    &:hover{
        background-color: $danger;
        color: #fff;
    }
}

.m-btn-success-outline{
    border: 1px solid $success;
    color: $success;
    &:hover{
        background-color: $success;
        color: #fff;
    }
}

.m-btn-secondary-outline,
.m-btn-success-outline,
.m-btn-danger-outline{
    &:hover{
        path{
            fill: #fff;
        }
    }
}

.m-btn-text{
    color: $gray700;
    &:hover{
        color: $black;
    }
}

.favourite-btn{
    flex-shrink: 0;
    &__icon{
        width: 1.5em;
        height: 1.5em;
        position: relative;
        img{
            @include centerAbsolute;
            width: 100%;
            display: block;
            transition: all .3s;
        }
        ._view2{
            opacity: 0;
        }
    }
    &._active{
        ._view2{
            opacity: 1;
        }
        ._view1{
            opacity: 0;
        }
    }
}

.doc-item{
    img{
        width: 1em;
    }
}
