@import "../vars.scss";
.breadcrumbs {
    padding: 0 0 1.5em;
    &__wrap{
        position: relative;
    }
    &__inner{
        display: flex;
        &:before{
            content: '';
            width: 1.75em;
            height: 1.75em;
            display: inline-block;
        }
    }
    &__item {
        font-size: 1em;
        line-height: 175%;
        position: relative;
        font-weight: 400;
        display: inline;
        &:last-child {
            color: $gray500;
        }
        &:not(:first-child) {
            &:before {
                content: '/';
                color: $black;
                margin: 0 .5em;
            }
        }
    }
    &__back{
        position: absolute;
        left: 0;
        top: .25em;
        width: 1.25em;
        display: block;
        svg{
            width: 100%;
            height: auto;
            display: block;
        }
        &:hover{
            path{
                fill: $primary;
            }
        }
    }
}

@media screen and (max-width: 992px){
    .breadcrumbs{
        display: none;
    }
}
