.product-desc {
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  &__left {
    width: calc(100% - 34.375em);
  }
  &__right {
    width: 32.875em;
    margin-left: 1.5em;
    img {
      width: 100%;
    }
  }
  &__text {
    margin-top: 0.5em;
    p {
      font-size: 1em;
      line-height: 150%;
    }
  }
}




@media screen and (max-width: 992px) {
  .product-desc {
    &__left {
      width: calc(100% - 20em);
    }
    &__right {
      width: 18.75em;
      margin-left: 1.25em;
    }
  }
}

@media screen and (max-width: 576px) {
  .product-desc {
    &__inner {
      flex-direction: column;
    }
    &__left {
      width: 100%;
    }
    &__right {
      order: -1;
      width: 100%;
      margin: 0 0 1.5em 0;
    }
  }

  .product-categories{
    ._section__grid{
      margin-top: .75em;
    }
  }
}
