.product-top {
  padding: 0 0 6.25em 0;
  &__inner {
    position: relative;
    margin-top: 2em;
  }
  &__img {
    border-radius: 1.25em;
    img {
      width: 100%;
      border-radius: 1.25em;
    }
  }
  &__items {
    position: absolute;
    left: 4.75em;
    bottom: -5.5625em;
    max-width: 18.0625em;
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    border-radius: 1.25em;
    background: #F8F9FA;
    box-shadow: 0 0.5em 1em 0 rgba(0, 0, 0, 0.15);
    padding: 1em;
  }
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 0.25em;
    border: 0.0625em solid #FDC305;
    background: #FDC305;
    padding: 0.375em 0.75em;
    &:hover {
      color: #212529;
    }
    i {
      flex-shrink: 0;
      width: 1em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1em;
      line-height: 120%;
      margin-left: 0.3125em;
    }
  }
}

.product-category {
  padding: 6.25em 0 3em;
  &__inner {
    display: flex;
  }
  &__sidebar {
    width: 19.125em;
    margin-right: 1.5em;
    &-inner {
      border-radius: 1.25em;
      background: #F8F9FA;
      padding: 1em;
    }
  }
  &__grid {
    width: calc(100% - 20.625em);
  }
  .grid-column3 {
    display: grid;
    grid-template-columns: repeat(3, calc(100% / 3 - 3em / 3));
    gap: 1.5em;
  }
  &__title {
    font-size: 1em;
    line-height: 120%;
    font-weight: 500;
  }
  &__subtitle {
    display: flex;
    align-items: center;
    margin-top: 1em;
    i {
      flex-shrink: 0;
      width: 1em;
      img {
        width: 100%;
      }
    }
    span {
      font-size: 1em;
      line-height: 120%;
      color: #495057;
      margin-left: 0.5em;
    }
  }
  &__list {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.5em;
    margin-top: 0.5em;
    margin-left: 1.5em;
    li {
      font-size: 1em;
      line-height: 150%;
    }
    ._active {
      color: #fdc305;;
    }
  }
}

@media screen and (max-width: 992px) {
  .product-top {
    padding: 0 0 2.3125em;
    &__inner{
      margin-top: .75em;
    }
    &__items {
      position: static;
      margin: 1.5em auto 0;
    }
  }

  .product-category {
    padding: 2.3125em 0 1.5em;
    &__grid {
      grid-template-columns: repeat(2, calc(50% - 1.5em / 2)) !important;
    }
  }
}

@media screen and (max-width: 576px) {
  .product-top {
    &__items {
      max-width: 100%;
    }
  }

  .product-category {
    &__inner {
      display: block;
    }
    &__sidebar {
      width: 100%;
      margin: 0 0 1.5em 0;
    }
    &__grid {
      width: 100%;
    }
  }
}
