.commercial{
  padding: 1em 3em;
  min-height: 5em;
  display: flex;
  align-items: center;
  background: linear-gradient(134deg, #9013FE 0%, #6A10F3 100%);
  &__img{
    width: calc(100% - 4.5em);
    img{
      width: 100%;
      display: block;
    }
  }
  &__close{
    width: 1.5em;
    height: 1.5em;
    flex-shrink: 0;
    margin-left: 3em;
    opacity: .5;
    &:hover{
      opacity: 1;
    }
    img{
      width: 100%;
      display: block;
    }
  }
}

.header{
  padding-bottom: 1.5em;
  position: relative;
  &__inner{
    position: relative;
    padding-top: 2.9375em;
  }
  &__top{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  &__top,
  &__middle,
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: .5em 0;
  }
  &__logo{
    display: block;
    width: 6.5em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__middle{
    justify-content: unset;
  }
  .m-btn-small{
    color: $gray700;
    @include lastM(0 .5em 0 0);
    svg{
      width: 1em;
    }
    &:hover{
      color: $primary;
      path{
        fill: $primary;
      }
    }
  }
  &__catalog-btn{
    cursor: pointer;
    flex-shrink: 0;
    margin-left: 1em;
    ._icon{
      width: 1em;
      height: 1em;
      flex-shrink: 0;
      position: relative;
      margin-right: .5em;
    }
    svg{
      position: absolute;
      top: 50%;
      left: 0;
      transform: translateY(-50%);
      margin: 0;
      width: 100%;
      height: auto;
    }
    ._view2{
      opacity: 0;
      width: 75%;
    }
  }
  &._open{
    .header__catalog-btn{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
}

.header-contacts,
.header-action{
  display: flex;
  align-items: center;
}

.header-action{
  margin-left: auto;
}

.header-search{
  flex-grow: 1;
  margin: 0 1em;
  display: flex;
  .fg{
    flex-grow: 1;
    input{
      border-radius: .25em 0 0 .25em;
    }
  }
  &__btn{
    padding: 0;
    width: 2.625em;
    border-radius: 0 .25em .25em 0;
    img{
      width: 1em;
    }
  }
}

.nav2{
  padding: 0;
  &__show,
  &__link{
    color: $gray700;
    padding: 0 1em;
    height: 2.5em;
    display: flex;
    align-items: center;
    transition: all .3s;
    span{
      font-size: 1em;
      line-height: 150%;
    }
    svg{
      width: .6875em;
      height: auto;
      margin-left: .25em;
    }
    &:hover{
      color: $primary;
      cursor: pointer;
    }
  }
  &__show{
    border-radius: .25em;
    &:hover{
      color: $gray700;

    }
  }
  &__item{
    position: relative;
    &:hover{
      .nav2__show{
        box-shadow: 0 0 0 .25em rgba(253, 195, 5, 0.25);
      }
      .dropdown{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  @media screen and (min-width: 993px){
    ._mob-hide{
      display: none;
    }
  }
}

.dropdown{
  position: absolute;
  top: 99%;
  padding-top: .25em;
  z-index: 5;
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  &__inner{
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.15);
    border-radius: .25em;
    min-width: 10em;
    padding: .5em 0;
  }
  &__link{
    display: flex;
    align-items: center;
    height: 2em;
    padding: 0 1em;
    span{
      font-size: 1em;
    }
  }
}

.mob-search,
.burger{
  display: none;
}

.menu{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  width: 100%;
  top: 100%;
  left: 0;
  background-color: #fff;
  z-index: 20;
  margin-top: -4em;
  padding-top: 1em;
  box-shadow: 0 1em 3em 0 rgba(0, 0, 0, 0.17);
  &__tabs{
    display: flex;
  }
  &__tab{
    height: 2.625em;
    display: flex;
    align-items: center;
    padding: 0 1em;
    color: $gray700;
    border: 1px solid transparent;
    border-bottom: none;
    border-radius: .25em .25em 0 0;
    cursor: pointer;
    transition: all .3s;
    background-color: #fff;
    position: relative;
    &._active{
      border-color: $gray300;
      pointer-events: none;
      &:before{
        opacity: 1;
      }
    }
    &:before{
      content: '';
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: #fff;
      opacity: 0;
      transition: inherit;
    }
  }
  &__content{
    overflow: hidden;
  }
  &__wrap{
    display: flex;
  }
  &._level2-open{
    .menu-collection{
      width: calc(100% - 32em);
      &__grid{
        grid-template-columns: repeat(3,calc(50% - .75em));
      }
    }
  }
  &._level3-open{
    .menu-collection{
      width: calc(100% - 48em);
      &__grid{
        grid-template-columns: repeat(3,100%);
      }
    }
  }
  &._level4-open{
    .menu-collection{
      width: calc(100% - 64em);
    }
  }
}

.menu-nav{
  flex-shrink: 0;
  display: flex;
  border-radius: 0 .25em .25em 0;
  border-right: 1px solid $gray300;;
  border-top: 1px solid $gray300;
  &__items{
    display: none;
    &._active{
      display: block;
    }
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 2.5em;
    padding: 0 1em;
    transition: all .3s;
    @include lastM(0 0 .5em);
    &._active,
    &:hover{
      background-color: $gray100;
      cursor: pointer;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
    img{
      width: .875em;
      display: block;
      flex-shrink: 0;
      margin-left: .25em;
    }
  }
}

.menu-level{
  padding-top: 1em;
  width: 16em;
  display: none;
  &._active{
    display: block;
  }
  &__title,
  &__back{
    display: none;
  }
}

.menu-collection{
  width: calc(100% - 16em);
  padding-left: 1.5em;
  &__grid{
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
}

.product-card2{
  &__title{
    min-height: 2.875em;
  }
  &__price1{
    text-decoration: line-through;
  }
}

.mob-nav{
  display: none;
}


@media screen and (max-width: 992px){
  .commercial{
    display: none;
  }

  .header{
    padding-bottom: 0;
    &__inner{
      padding-top: 0;
    }
    &__logo{
      width: 3.875em;
    }
    &__catalog-btn{
      display: none;
    }
    &__middle{
      justify-content: space-between;
      position: relative;
      z-index: 11;
      background-color: #fff;
      box-shadow: 0px -1px 0px 0px rgba(0, 0, 0, 0.13) inset;
      @include fullScreen;
    }
    &._open-burger{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .header__mob-menu{
        opacity: 1;
        transform: translateX(0);
      }
    }
    &._open{
      .menu{
        transform: translateX(0);
      }
    }
    &__mob-menu{
      position: fixed;
      background-color: #fff;
      z-index: 10;
      left: 0;
      top: 0;
      height: $fullHeight;
      width: 100%;
      padding: 4.125em .75em 2em;
      overflow-y: auto;
      transition: all .4s;
      opacity: 0;
      transform: translateX(-100%);
    }
    &__top{
      position: static;
      padding: .5em;
      border-radius: 1.25em;
      background-color: $gray100;
      .nav{
        display: none;
      }
      .site-config{
        flex-direction: column;
        align-items: flex-start;
      }
      .m-btn-small{
        padding: 0 .75em;
        height: 2.375em;
        @include lastM(0);
        &:not(:first-child){
          margin-top: .5em;
        }
        span{
          font-size: 1em;
        }
      }
    }
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
      margin-top: .75em;
    }
  }

  .header-search,
  .header-action{
    display: none;
  }

  .mob-search,
  .burger{
    display: flex;
    width: 3.125em;
    height: 2.125em;
    position: relative;
    img,
    svg{
      @include centerAbsolute;
      width: 1.5em;
      height: auto;
    }
    ._view2{
      opacity: 0;
    }
  }

  .header-contacts{
    flex-direction: column-reverse;
    align-items: flex-end;
    &__btn{
      display: none;
    }
  }

  .nav2{
    &__item{
      width: 100%;
    }
    &__show{
      justify-content: space-between;
      box-shadow: none !important;
      &:after{
        content: '';
        display: block;
        flex-shrink: 0;
        width: .875em;
        height: .875em;
        background-image: url(../img/arrow2.svg);
        @include bgImgSize(100%);
        margin-left: 1em;
        transition: all .3s;
      }
      svg{
        display: none;
      }
      &._active{
        &:after{
          transform: rotate(-180deg);
        }
      }
    }
    .dropdown{
      position: static;
      opacity: 1;
      pointer-events: unset;
      padding: 0;
      z-index: unset;
      transition: unset;
      display: none;
      &__inner{
        padding: 0 0 0 2em;
        border: none;
      }
    }
  }

  .menu{
    background-color: #fff;
    z-index: 10;
    left: 0;
    top: 0;
    margin: 0;
    height: $fullHeight;
    width: 100%;

    transition: all .4s;
    transform: translateX(-100%);
    &__tabs{
      display: none;
    }
  }

  .menu-nav{
    border: none;
    &__items{
      height: 100%;
    }
  }

  .menu-level{
    background-color: #fff;
    padding: 4.125em .75em 6em;
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
    display: block !important;
    &._active{
      opacity: 0;
      pointer-events: none;
    }
    &._active2{
      opacity: 1;
      pointer-events: unset;
      overflow-y: auto;
      z-index: 10;
    }
    &__title{
      margin-bottom: .75em;
      display: block;
    }
    &__back{
      display: flex;
      align-items: center;
      height: 3.5em;
      padding: 0 1em;
      color: $gray700;
      background-color: $gray100;
      border-radius: 1.25em;
      margin-bottom: .75em;
      img{
        width: 1em;
        flex-shrink: 0;
        margin-right: .5em;
      }
      span{
        font-size: 1em;
        line-height: 150%;
      }
    }
  }

  .menu-collection{
    display: none;
  }

  .mob-nav{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: #fff;
    z-index: 11;
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 0.13) inset;
    padding: .5em 0;
    display: block;
    &__inner{
      display: grid;
      grid-template-columns: repeat(5,3.9875em);
      justify-content: center;
      gap: .5em;
    }
    &__btn{
      display: flex;
      flex-direction: column;
      align-items: center;
      color: $gray700;
      svg{
        height: 1.375em;
        width: auto;
        margin-bottom: .25em;
      }
      span{
        font-size: .875em;
        line-height: 150%;
      }
      &._active{
        color: $secondary;
        path{
          fill: $secondary;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .header{

  }
}
