.account{
  padding-bottom: 3em;
  &__grid{
    margin-top: 2em;
  }
  &__content{
    grid-column: 2/-1;
  }
}

.account-sidebar{
  position: sticky;
  top: 1em;
  padding: 1em;
  border-radius: 1.25em;
  background-color: $gray100;
  &__show,
  &__link{
    display: flex;
    align-items: center;
    min-height: 2.5em;
    padding: .5em 1em;
    transition: all .3s;
    color: $gray700;
    &._active,
    &:hover{
      background-color: $yellow100;
      color: $black;
      cursor: pointer;
    }
    img{
      width: .875em;
      flex-shrink: 0;
    }
    span{
      font-size: 1em;
      line-height: 150%;
    }
  }
  &__icon{
    margin-right: .5em;
  }
  &__arrow{
    margin-left: auto;
    transition: all .4s;
  }
  &__hidden{
    padding: .75em .5em .5em 2em;
    display: none;
  }
  &__links{
    @include grid100gap(.5em);
  }
  &__show{
    &._active{
      .account-sidebar__arrow{
        transform: rotate(-180deg);
      }
    }
  }
}

.user-data{
  @include grid100gap(3em);
  max-width: 26em;
  &__fields{
    margin-top: 1.5em;
    @include grid100gap(1em);
  }
  &__nav{
    display: flex;
  }
}

.radios-group{
  &__wrap{
    display: flex;
    &:not(:first-child){
      margin-top: .5em;
    }
  }
  .m-radio{
    margin-right: 1em;
  }
}

.loyalty{
  max-width: 46.625em;
  @include grid100gap(3em);
}

.loyalty-card{
  border-radius: 1.25em;
  background-color: $gray100;
  padding: 1em;
  @include grid100gap(.5em);
  &__top{
    display: flex;
    align-items: center;
  }
  &__icon{
    display: block;
    margin-right: .5em;
    width: 1em;
    flex-shrink: 0;
  }
  &__hint{
    display: none;
  }
  &__nav2,
  &__nav{
    display: flex;
    margin-top: .5em;
  }
  &__nav2{
    .m-btn{
      margin-right: .5em;
    }
  }
  &__history{
    margin-top: 1.5em;
    display: flex;
    &-btn{
      display: flex;
      height: 2em;
      align-items: center;
      color: $black;
      img{
        width: 1em;
        flex-shrink: 0;
        display: block;
        margin-right: .5em;
      }
      &:hover{
        opacity: .8;
      }
    }
  }
  @media screen and (min-width: 993px){
    .modal{
      height: unset;
      display: block;
      position: static;
      opacity: 1;
      pointer-events: unset;
      &__top,
      &__bottom,
      &__bg{
        display: none;
      }
      &__dialog{
        width: unset;
        border: none;
        border-radius: 0;
        background-color: transparent;
      }
      &__content{
        padding: 0;
        border: none;
      }
    }
  }
  .modal{
    &__content{
      a{
        text-decoration: underline;
      }
    }
  }
  &__address{
    width: calc(100% + 2em);
    margin: 0 0 -1em -1em;
    padding-top: .5em;
  }
  &__links{
    margin-top: 1em;
    padding: .25em 0;
    display: flex;
    .primary-link{
      margin-right: 1em;
    }
  }
}

.favourite-field,
.loyalty-link{
  display: flex;
  margin-top: .5em;
  &__text{
    height: 2.375em;
    flex-shrink: 0;
    padding: 0 .75em;
    display: flex;
    align-items: center;
    border-radius: .25em 0 0 .25em;
    border: 1px solid $gray400;
    border-right: none;
    background: $gray200;
  }
  .fg{
    flex-grow: 1;
    input{
      border-radius: 0;
    }
  }
  &__copy{
    width: 2.625em;
    height: 2.375em;
    flex-shrink: 0;
    border-radius: 0  .25em .25em 0;
    border: 1px solid $gray400;
    border-left: none;
    background: $gray200;
    &:hover{
      background: $gray400;
    }
    img{
      width: 1em;
    }
  }
  &__clear{
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 1px;
    padding: 0 .75em 0 .5em;
    height: calc(100% - 2px);
    background-color: #fff;
    img{
      width: 1em;
    }
  }
}

.favourite{
  @include grid100gap(3em);
  max-width: 53.5em;
  &__items{
    @include grid100gap(1.5em);
  }
}

.favourite-group{
  &__nav{
    display: none;
  }
  &__bottom{
    margin-top: 1.5em;
  }
}

.favourite-card{
  padding: 1em;
  background-color: $gray100;
  border-radius: .25em;
  &__top{
    display: grid;
    grid-template-columns: 6em calc(100% - 7em);
    gap: 1em;
  }
  &__img{
    height: 6em;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      display: block;
    }
  }
  &__info{
    display: flex;
    align-items: center;
  }
  .badge{
    margin-left: .5em;
    margin-right: .5em;
  }
  .favourite-btn{
    margin-left: auto;
  }
  &__content{
    display: flex;
    flex-direction: column;
  }
  &__info2{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    margin-top: auto;
    padding-top: 1em;
  }
  ._last{
    text-align: right;
  }
  .favourite-field{
    width: 12.3125em;
    margin: 0 1em;
  }
  .props{
    margin-top: 1em;
    &__items{
      margin-top: 1em;
      display: none;
    }
  }
  &._show-props{
    .props__btn{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
}

.props{
  &__btn{
    width: 100%;
    ._view2{
      display: none;
    }
  }
}

.props-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  @include lastM(0 0 .5em);
  &:before{
    content: '';
    flex-grow: 1;
    min-width: 1.9375em;
    height: 1px;
    border-top: 1px dashed rgba(33, 37, 41, 0.25);
    margin: 0 1em;
  }
  &__title{
    order: -1;
  }
}

.favourite-products{
  @include grid100gap(1.5em);
}

.address-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: .5em 1em;
  border-top: 1px solid rgba(0, 0, 0, 0.13);
  &__text{
    display: flex;
    align-items: center;
  }
  &__delete{
    width: 1em;
    flex-shrink: 0;
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $danger;
      }
    }
  }
}

.subscribes{
  @include grid100gap(3em);
  &__nav{
    display: flex;
  }
}

.subscribes-group{
  &__items{
    margin-top: 1.5em;
    @include grid100gap(.375em);
  }
}

.company-hint{
  display: flex;
  align-items: center;
  padding: 1em;
  border-radius: 0.25em;
  border: 1px solid $info;
  background: #E0F0FE;
  color: $infoTextColor;
  img{
    width: 1em;
    flex-shrink: 0;
    display: block;
    margin-right: .5em;
  }
}

.company-req{
  padding: 1em;
  border-radius: 1.25em;
  background-color: $gray100;
  @include grid100gap(1em);
}

.req-item{
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__nav{
    display: flex;
    align-items: center;
    .m-btn{
      margin-left: .5em;
    }
    svg{
      display: none;
    }
  }
}

.companies{
  overflow: hidden;
  border: $border;
  border-radius: .25em;
  box-shadow: 0px -1px 0px 0px rgba(0, 0, 0, 0.13) inset, 0px 1px 0px 0px rgba(0, 0, 0, 0.13) inset;
}

.company-card{
  &:not(:first-child){
    border-top: $border;
  }
  &__show{
    padding: 1em 1.25em;
    display: flex;
    align-items: center;
    transition: all .3s;
    cursor: pointer;
    &:hover,
    &._active{
      background: #FDF9EB;
    }
    &._active{
      img{
        transform: rotate(180deg);
      }
    }
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-left: .5em;
      transition: all .4s;
    }
  }
  .badge{
    border-radius: .25em;
  }
  &__right{
    margin-left: auto;
    padding-left: .5em;
    display: flex;
    align-items: center;
  }
  &__hidden{
    padding: 1em 1.25em;
    border-top: $border;
    display: none;
  }
  &__items{
    border: 1px solid rgba(0,0,0,.13);
    border-radius: .25em;
    overflow: hidden;
    .address-item{
      &:first-child{
        border-top: none;
      }
    }
  }
  .spinner{
    margin-right: .5em;
  }
}


@media screen and (max-width: 992px){
  .account{
    padding: 1em 0 1.5em;
    &__grid{
      margin-top: .75em;
      grid-template-columns: 100%;
    }
    &__content{
      grid-column: 1/-1;
    }
  }

  .user-data,
  .loyalty{
    gap: 1.5em;
  }

  .loyalty-card{
    &__hint{
      display: flex;
      margin-left: auto;
      padding-left: .5em;
      img{
        width: 1em;
      }
    }
  }

  .favourite{
    gap: .75em;
    &__items{
      gap: .75em;
    }
  }

  .favourite-group{
    &__top{
      padding: 1em;
      border-radius: 1.25em;
      background-color: $gray100;
      display: flex;
      align-items: center;
      justify-content: space-between;
      .text20,
      .text24{
        font-size: 1em;
        line-height: 150%;
        font-weight: 400;
      }
      &._active{
        .favourite-group__arrow{
          transform: rotate(-180deg);
        }
      }
    }
    &__nav{
      display: flex;
      align-items: center;
    }
    &__arrow{
      width: 1em;
      flex-shrink: 0;
      display: block;
      margin-left: .5em;
      transition: all .4s;
    }
    &__num{
      @include center;
      border-radius: 6.25em;
      background-color: $primary;
      color: #fff;
      padding: 0 .49em;
      min-width: 1.4125em;
      height: 1.275em;
    }
    &__bottom{
      margin-top: .75em;
      display: none;
    }
  }

  .favourite-products{
    gap: .75em;
    &__title{
      display: none;
    }
  }

  .subscribes{
    gap: 1.5em;
  }

  .subscribes-group{
    &__items{
      margin-top: 1em;
    }
  }
}

@media screen and (max-width: 576px){
  .loyalty-card{
    &._border{
      border-radius: .25em;
      background-color: transparent;
      border: 1px solid $gray400;
    }
    &__nav2{
      @include grid100gap(.5em);
      .m-btn{
        margin: 0;
        width: 100%;
      }
    }
    &__history{
      width: calc(100% + 2em);
      margin: 1em 0 -1em -1em;
      height: 2.5625em;
      align-items: center;
      padding: 0 1em;
      border-top: 1px solid $gray400;
    }
    &__links{
      margin: 0;
      padding: 0;
      flex-direction: column;
      align-items: flex-start;
      .primary-link{
        margin: 1em 0 0;
      }
    }
  }

  .favourite-card{
    position: relative;
    &__top{
      grid-template-columns: 100%;
    }
    &__img{
      width: 6em;
    }
    .favourite-btn,
    .badge{
      position: absolute;
      top: 1em;
      right: 1em;
      margin: 0;
    }
    .badge{
      right: 4em;
    }
    &__title{
      position: absolute;
      left: 8em;
      top: 1em;
      width: calc(100% - 15.22em);
      word-break: break-word;
      @include truncate(3);
    }
    &__info2{
      flex-wrap: wrap;
      padding: 0;
      margin: 0;
    }
    .favourite-field{
      order: 2;
      margin: 1em 0 0;
      width: 100%;
    }
    .product-card2__price2{
      font-size: 1em;
      line-height: 150%;
    }
  }

  .favourite{
    .grid-column4{
      grid-template-columns: repeat(2,calc(50% - .5em));
      column-gap: 1em;
    }
  }

  .req-item{
    .m-btn{
      width: 2em;
      height: 2em;
      span{
        display: none;
      }
      svg{
        display: block;
        width: 1em;
        margin: 0;
      }
    }
  }
}
